@use "styles/colors";
@use "styles/mixins";
@use "styles/shadows";

$width: 80%;

.navigation {
  background-color: white;
  border-bottom-right-radius: 6px;
  box-shadow: shadows.$shadow1;

  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: $width;
  display: flex;
  gap: 40px;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;

  transition: left 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);

  @include mixins.tablet {
    display: flex;
  }

  .close-btn {
    border-radius: 50%;
    background-color: white;
    width: 25px;
    height: 25px;
  }

  .menu-list {
    display: flex;
    flex-direction: column;
    gap: 15px;

    .menu-item {
      margin-right: 40px;

      .menu-link {
        color: colors.$darkNavy;
      }
    }
  }

  .button {
    background-color: white;
    color: colors.$complementaryGreen;
  }
}

.navigation--hidden {
  @extend .navigation;

  left: (-$width);
}
